html,
body {
  padding: 0;
  margin: 0; }

body {
  min-width: 320px;
  font-size: 17px;
  line-height: 1.7;
  font-family: "Fira Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  background-size: auto 100%;
  background-repeat: no-repeat; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@media (max-width: 1360px) {
  body {
    background-position: center top; } }

@media (min-width: 1360px) {
  body {
    background-size: 100%; } }

@media (min-width: 768px) {
  body {
    background-image: url("../img/bg-hero.jpg"); } }

@media (min-width: 768px) and (-webkit-min-device-pixel-ratio: 1.5), (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  body {
    background-image: url("../img/bg-hero@2x.jpg"); } }

@media (max-width: 767px) {
  body {
    background-image: url("../img/bg-hero-small.jpg");
    background-size: 912px auto;
    background-position: center 33px; } }

@media (max-width: 767px) and (-webkit-min-device-pixel-ratio: 1.5), (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  body {
    background-image: url("../img/bg-hero-small@2x.jpg"); } }

.container {
  max-width: 1142px;
  margin: 0 auto;
  padding: 0 20px; }

@media (max-width: 767px) {
  .container {
    padding: 0 16px; } }

.header {
  padding: 32px 0;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.2; }

.header__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__tel {
  color: #000000;
  text-decoration: none; }

.header__tel:hover {
  color: #ffc341; }

.header__logo {
  width: 137px;
  height: 48px;
  margin-right: 23px; }

.header__callback-link {
  font-size: 13px;
  letter-spacing: 0.2px;
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  border-bottom: 1px dashed #000000; }

.header__callback-link:hover {
  color: #ffc341;
  border-color: #ffc341; }

@media (max-width: 767px) {
  .header {
    padding-top: 21px;
    padding-bottom: 20px;
    font-size: 14px; }
  .header__container {
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-right: 7px; }
  .header__logo {
    -webkit-box-ordinal-group: 0;
        -ms-flex-order: -1;
            order: -1;
    width: 120px;
    height: 42px;
    margin-bottom: -16px; }
  .header__callback {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
    width: 100%;
    margin-right: 1px;
    font-size: 13px; }
  .header__callback-link {
    letter-spacing: normal; } }

.hero {
  padding-top: 427px;
  padding-bottom: 146px; }

.hero__description {
  max-width: 350px;
  margin: 0 auto 32px;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.2;
  color: #ffffff;
  text-align: center; }

.hero__scroll-indicator {
  position: relative;
  display: block;
  width: 76px;
  height: 76px;
  margin: 0 auto calc(131px - 76px);
  background-image: url("../img/dashed-round.svg");
  background-repeat: no-repeat;
  background-size: 76px 76px;
  background-position: top center; }

.hero__scroll-indicator::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 52px;
  height: 52px;
  margin-top: -27px;
  margin-left: -27px;
  background-image: url("../img/icon-mouse.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 17px 26px;
  border-radius: 50%;
  border: 1px solid #ffffff; }

.hero__scroll-indicator::after {
  content: "";
  position: absolute;
  top: 76px;
  left: 50%;
  width: 1px;
  height: 55px;
  background-color: #ffffff; }

@media (max-width: 767px) {
  .hero {
    padding-top: 252px;
    padding-bottom: 85px; }
  .hero__description {
    width: auto;
    font-size: 20px; } }
